.app-sidebar{position:fixed;top:50px;left:0;bottom:0;padding:20px 0px 50px;width:180px;min-width:45px;max-height:100vh;height:100%;z-index:1023;background:#FFF;box-shadow:0 2px 3px rgba(17,17,17,0.1),0 0 0 1px rgba(17,17,17,0.1);overflow-y:auto;overflow-x:hidden}@media screen and (max-width: 768px){.app-sidebar{-webkit-transform:translate3d(-180px, 0, 0);transform:translate3d(-180px, 0, 0)}}.app-sidebar .collapse{display:none}.app-sidebar .collapse.in{display:block}.app-sidebar .collapsing{position:relative;height:0;overflow:hidden;-webkit-transition:height .377s ease;transition:height .377s ease}.app-sidebar .menu-list li{overflow:hidden}.app-sidebar .menu-list li a{position:relative;white-space:nowrap}.app-sidebar .menu-list li a .is-angle{position:absolute;right:10px;-webkit-transition:-webkit-transform .377s ease;transition:-webkit-transform .377s ease;transition:transform .377s ease;transition:transform .377s ease, -webkit-transform .377s ease}.app-sidebar .menu-list li a[aria-expanded="true"] .is-angle{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.app-sidebar .menu-list li a[aria-expanded="true"]+.collapse{display:block;visibility:visible}.app-sidebar .menu-list li ul{margin:0 10px;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}.app-sidebar .menu-list li ul>li:first-child{padding-top:10px}.app-sidebar .menu-list li ul>li:last-child{padding-bottom:10px}



